body {
  font-family: 'Roboto', sans-serif;
  font-size: 16px;
  margin: 0;
  padding: 0;
}

html {
    scroll-behavior: smooth;
}

.link {
  color: inherit; /* blue colors for links too */
  text-decoration: inherit; /* no underline */
}

.hidden {
  display: none;
}

.section-head {
  margin-top: 40px;
  text-align: center;
}

.divider {
  width: 30px;
  height: 4px;
  margin: 20px auto;
  margin-bottom: 80px;
  background: $default;
}

.hide {
  display: none;
}
.mt50 {
  margin-top: 50px;
}
